let hasNative = false

let api = null

function callHandler (funcName, data) {
  if (hasNative) {
    return new Promise(resolve => {
      api.callHandler(funcName, data, function (res) {
        resolve(res)
      })
    })
  } else {
    return Promise.reject({ code: -1 })
  }
}

function broadcast (event, data) {

}

const JsNativeApi = {
  _bluetooth: 'waiting',
  get bluetooth () {
    if (this._bluetooth === 'waiting') {
      return true
    } else {
      return this._bluetooth === 'open'
    }
  },
  isValid () {
    return hasNative
  },
  getNativeOS () {
    let os = window.localStorage.getItem('os')
    if (os) {
      return Promise.resolve(os)
    } else {
      return callHandler('getNativeOS', null).then(os => {
        window.localStorage.setItem('os', os)
        return os
      })
    }
  },
  openSysSetting () {
    return callHandler('openSysSetting', null).then(result => {
      return result === 'true' ? true : false
    })
  },
  scan () {
    return callHandler('scanQRCode', null).then(result => {
      return JSON.parse(result)
    })
  },
  ringUp (phoneNumber) {
    if (phoneNumber) {
      return callHandler('ringUp', {
        phoneNumber
      }).then(result => {
        return result === 'true' ? true : false
      })
    } else {
      return Promise.reject({ code: 101 })
    }
  },
  isBraceletConnected () {
    return callHandler('isBraceletConnected', null).then(result => {
      return JSON.parse(result)
    })
  },
  connectBracelet (mac) {
    return callHandler('connectBracelet', {
      mac
    }).then(result => {
      return result === 'true' ? true : false
    })
  },
  disConnectBracelet (id) {
    return callHandler('disConnectBracelet', null).then(result => {
      return result === 'true' ? true : false
    })
  }
}

function proxy (bridge) {
  hasNative = true
  api = bridge

  callHandler('isBluetoothOpen', null).then(function (open) {
    let state = open ? 'open' : 'close'
    if (JsNativeApi._bluetooth !== state) {
      JsNativeApi._bluetooth = state
      broadcast('bluetooth_changed', open)
    }
  })
}

window.JsNativeApi = JsNativeApi

export default JsNativeApi

export function inject () {
  return new Promise(resolve => {
    window.setupWebViewJavascriptBridge = function (cb) {
      if (window.WebViewJavascriptBridge) {
        return cb(window.WebViewJavascriptBridge)
      } else {
        // 兼容android bridge
        document.addEventListener('WebViewJavascriptBridgeReady', function () {
          cb(window.WebViewJavascriptBridge)
        }, false)
      }
      if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(cb) }
      window.WVJBCallbacks = [cb]
      let WVJBIframe = document.createElement('iframe')
      WVJBIframe.style.display = 'none'
      WVJBIframe.src = 'https://__bridge_loaded__'
      document.documentElement.appendChild(WVJBIframe)
      setTimeout(function () { document.documentElement.removeChild(WVJBIframe) }, 0)
    }
    window.setupWebViewJavascriptBridge(function (bridge) {
      proxy(bridge)
      resolve()
    })
  })
}
