import mock from './mock.js'

let bluetooth = false

let mac = null

function delay (cb, result, time = 2000) {
  setTimeout(function () {
    cb(result)
  }, time)
}

function callHandler (funcName, data, cb) {
  switch (funcName) {
    case 'getNativeOS':
      cb('android')
      break
    case 'isBluetoothOpen':
      cb(bluetooth)
      break
    case 'openSysSetting':
      bluetooth = true
      cb(true)
      break
    case 'scanQRCode':
      delay(cb, JSON.stringify(Math.random() > 0.5 ? {
        type: 0
      } : {
        type: 1,
        message: `G1MW99301825C#${ mock.random.string('upper', 12) }#42C89500`
      }))
      break
    case 'ringUp':
      console.log(`will call ${ data.phoneNumber }`)
      break
    case 'isBraceletConnected':
      delay(cb, JSON.stringify(mac ? {
        type: true,
        mac
      } : {
        type: false
      }))
      break
    case 'disConnectBracelet':
      mac = null
      delay(cb, 'true')
      break
    case 'connectBracelet':
      mac = data.mac
      delay(cb, 'true')
      break
    default:
      break
  }
}

setTimeout(function () {
  if (!window.JsNativeApi.isValid() && window.WVJBCallbacks) {
    window.WVJBCallbacks[0]({
      callHandler: callHandler
    })
  }
}, 2000)

export default {
  base_url: mock.base_url, // 为'/'时表示通过vue.config.js设置代理
  timeout: 15000
}
